:root {
  --color-primary: hsl(200, 100%, 30%);
  --color-primary--light: hsl(200, 100%, 40%);
  --color-primary--dark: hsl(200, 100%, 20%);
  --color-secondary: hsl(330, 75%, 45%);
  --color-secondary--light: hsl(330, 75%, 55%);
  --color-secondary--dark: hsl(330, 75%, 35%);
  --color-white: hsl(0, 0%, 100%);
  --color-grey: hsl(0, 0%, 50%);
  --color-grey--light: hsl(0, 0%, 75%);
  --color-grey--dark: hsl(0, 0%, 25%);
  --color-black: hsl(0, 0%, 0%);
}

.sg-swatch {
  border: 1px solid var(--color-grey--light);
}

/**
 * @file
 * This file sets up the basic vertical rhythm for our sites.
 *
 * Notes:
 * - In general, we are using vertical rhythm settings from www.gridlover.net
 * - In general, what they call h2, we call h1; what they call h3, we call h2, etc.
 * - This file sets up the base font size for the html element, and then the
 *   line heights and bottom margin for items.
 * - The font sizes for headings is posted in _headings.scss.
 */
html {
  box-sizing: border-box;
  line-height: 1.5;
  scroll-behavior: smooth;
}

html strong {
  font-weight: 900;
}

html *,
html *::before,
html *::after {
  box-sizing: inherit;
  margin-top: 0;
}

@media screen and (prefers-reduced-motion: reduce) {
  html {
    scroll-behavior: auto;
  }
}

body {
  margin: 0;
}

h1,
.h1 {
  margin-bottom: 2rem;
  line-height: 1.25;
}

h2,
.h2 {
  margin-bottom: 1rem;
}

h3,
.h3 {
  margin-bottom: 1rem;
}

h4,
.h4 {
  margin-bottom: 1rem;
}

h5,
.h5 {
  margin-bottom: 1rem;
}

p,
ul,
ol,
pre,
table,
blockquote {
  margin-bottom: 1rem;
}

ul ul,
ol ol,
ul ol,
ol ul {
  margin-bottom: 0;
}

sub,
sup {
  position: relative;
  vertical-align: baseline;
  line-height: 0;
}

sup {
  top: -0.5rem;
}

sub {
  bottom: -0.25rem;
}

/**
 * Breakpoint Configuration
 * @see https://github.com/Team-Sass/breakpoint/wiki
 *
 * Usage:
 * display: none; // hidden for mobile
 * @include breakpoint($bp--small-up) {
 *   display: block; // shown above 400
 * }
 */
.layout-container {
  position: relative;
}

.layout-contained {
  max-width: 960px;
  margin-right: auto;
  margin-left: auto;
}

.layout-contained--medium {
  max-width: 768px;
}

.layout-contained--small {
  max-width: 640px;
}

.u-spacing-down {
  margin-bottom: 20px;
}

.u-spacing-up {
  margin-top: 20px;
}

.u-gap-down {
  margin-bottom: 5px;
}

.u-gap-up {
  margin-top: 5px;
}

.padding {
  padding: 16px;
}

.padding-horizontal {
  padding-right: 16px;
  padding-left: 16px;
}

.padding-vertical {
  padding-top: 16px;
  padding-bottom: 16px;
}

.margin-bottom {
  margin-bottom: 24px;
}

@media (min-width: 640px) {
  .margin-bottom {
    margin-bottom: 48px;
  }
}

@media (min-width: 768px) {
  .margin-bottom {
    margin-bottom: 72px;
  }
}

.margin-top {
  margin-top: 24px;
}

@media (min-width: 640px) {
  .margin-top {
    margin-top: 48px;
  }
}

@media (min-width: 768px) {
  .margin-top {
    margin-top: 72px;
  }
}

.padding-bottom {
  padding-bottom: 24px;
}

@media (min-width: 640px) {
  .padding-bottom {
    padding-bottom: 48px;
  }
}

@media (min-width: 768px) {
  .padding-bottom {
    padding-bottom: 72px;
  }
}

.padding-top {
  padding-top: 24px;
}

@media (min-width: 640px) {
  .padding-top {
    padding-top: 48px;
  }
}

@media (min-width: 768px) {
  .padding-top {
    padding-top: 72px;
  }
}

h1,
.h1,
h2,
.h2,
h3,
.h3,
h4,
.h4,
h5,
.h5,
h6,
.h6 {
  color: var(--color-grey--dark);
  font-weight: 800;
}

h1,
.h1 {
  font-size: 36px;
}

@media screen and (min-width: 380px) {
  h1,
  .h1 {
    font-size: calc( 36px + strip-unit(18px) * (100vw - 380px) / strip-unit(790px));
  }
}

@media screen and (min-width: 1170px) {
  h1,
  .h1 {
    font-size: 54px;
  }
}

h2,
.h2 {
  font-size: 24px;
}

@media screen and (min-width: 380px) {
  h2,
  .h2 {
    font-size: calc( 24px + strip-unit(12px) * (100vw - 380px) / strip-unit(790px));
  }
}

@media screen and (min-width: 1170px) {
  h2,
  .h2 {
    font-size: 36px;
  }
}

h3,
.h3 {
  font-size: 20px;
}

@media screen and (min-width: 380px) {
  h3,
  .h3 {
    font-size: calc( 20px + strip-unit(4px) * (100vw - 380px) / strip-unit(790px));
  }
}

@media screen and (min-width: 1170px) {
  h3,
  .h3 {
    font-size: 24px;
  }
}

h4,
.h4 {
  font-variant: small-caps;
}

h4,
.h4 {
  font-size: 1rem;
}

@media screen and (min-width: 380px) {
  h4,
  .h4 {
    font-size: calc( 1rem + strip-unit(0rem) * (100vw - 380px) / strip-unit(790px));
  }
}

@media screen and (min-width: 1170px) {
  h4,
  .h4 {
    font-size: 1rem;
  }
}

h5,
.h5 {
  text-decoration: underline;
  font-variant: small-caps;
}

h5,
.h5 {
  font-size: 1rem;
}

@media screen and (min-width: 380px) {
  h5,
  .h5 {
    font-size: calc( 1rem + strip-unit(0rem) * (100vw - 380px) / strip-unit(790px));
  }
}

@media screen and (min-width: 1170px) {
  h5,
  .h5 {
    font-size: 1rem;
  }
}

h6,
.h6 {
  font-size: 1rem;
}

@media screen and (min-width: 380px) {
  h6,
  .h6 {
    font-size: calc( 1rem + strip-unit(0rem) * (100vw - 380px) / strip-unit(790px));
  }
}

@media screen and (min-width: 1170px) {
  h6,
  .h6 {
    font-size: 1rem;
  }
}

html {
  color: var(--color-grey--dark);
  font-family: -apple-system, blinkmacsystemfont, "Segoe UI", roboto, oxygen-sans, ubuntu, cantarell, "Helvetica Neue", sans-serif;
}

::selection {
  color: var(--color-black);
  background: var(--color-secondary);
  background: rgba(var(--color-secondary), 0.75);
}

::-moz-selection {
  color: var(--color-black);
  background: var(--color-secondary);
  background: rgba(var(--color-secondary), 0.75);
}

body.cke_editable {
  padding: 10px;
  color: var(--color-black);
  font-family: -apple-system, blinkmacsystemfont, "Segoe UI", roboto, oxygen-sans, ubuntu, cantarell, "Helvetica Neue", sans-serif;
  font-size: 1rem;
}
